html {
  font-size: 15px;
}
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  color: #2B2A2C;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.container {
  width: 100%;
  max-width: 1200px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0 auto;
}
.header {
  padding: 1.2rem 0;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.user {
  display: flex;
}
.user__avatar {
  width: 57px;
  height: 57px;
}
.user__avatar img {
  border-radius: 50%;
}
.user__content {
  padding-left: 9px;
}
.user__name {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  color: #2B2A2C;
  padding-bottom: 3px;
}
.user__prof {
  font-size: 12px;
  opacity: 0.7;
}
.nav {
  display: flex;
  height: 100%;
}
.nav__link {
  font-size: 14px;
  color: #2B2A2C;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  margin: auto 0;
  padding: 10px 30px;
  border: 2px solid transparent;
  border-radius: 20px;
  margin: 0 5px;
  transition: border 0.2s linear;
  transform: background 0.2s linear;
}
.nav__link:hover,
.nav__link.active {
  border: 2px solid #2B2A2C;
  background-color: rgba(243, 236, 236, 0.294);
}

